#home {
    position: relative;
    background: url('../../Assets/bgIMG.png');
    background-size: cover;
    background-repeat: repeat;
    background-position: center;

    .app__wrapper {
        padding: 0;

        .copyright {
            display: none;
        }
    }
}

.app__header {
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;
    padding: 6rem 2rem 0rem;

    @media screen and (min-width: 2000px) {
        padding-top: 8rem;
    }

    @media screen and (max-width: 1200px) {
        flex-direction: column;
    }

    @media screen and (max-width: 450px) {
        padding: 6rem 1rem 2rem; 
    }
}

.app_header-info {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
    margin: 0 2rem;

    @media screen and (max-width: 2000px) {
        width: 100%;
        margin-right: 1.2rem;
        margin-top: 6rem;
    }

    @media screen and (min-width: 2000px) {
        justify-content: center;
        align-items: center;
    }
}

.app__header-badge {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp, .tag-cmp {
        padding: 1rem 2rem;
        border: var(black);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px rgba($color: #000000, $alpha: 0.1);
        background-color: #fff;
    }

    .head-text {
        font-size: 1.5rem;

        @media screen and (min-width: 2000px) {
            font-size: 4rem;
        }
        
        @media screen and (max-width: 450px) {
            font-size: 2rem;
        }
    }

    .p-text {
        font-size: 0.9rem;
        font-weight: 500;

        @media screen and (min-width: 2000px) {
            font-size: 1.75rem;
        }
      
        @media screen and (min-width: 2400px) {
            font-size: 2.2rem;
        }
    }

    .tag-cmp {
        flex-direction: column;
        margin-top: 1rem;

        p {
            width: 100%;
            text-transform: uppercase;
            text-align: right;
        }
    }

    span {
        font-size: 2.5rem;

        @media screen and (max-width: 2000px) {
            font-size: 5rem;
        }
    }

    @media screen and (max-width: 1200px) {
        justify-content: flex-start;
        align-items: flex-start;
    }
}

.app__header-circles {
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    margin-left: 1rem;
    height: 100%;

    div {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: var(--white-color);
        box-shadow: 0 0 20px rgba($color: #000000, $alpha: 0.1);
        
        img {
            width: 100%;
            height: 100%;
        }
    }

    div:nth-child(1) {
        margin-left: 0rem;
        width: 60px;
        height: 60px;
    }
    div:nth-child(2) {
        margin-left: 1.75rem;
        width: 80px;
        height: 80px;
    }
    div:nth-child(3) {
        margin-left: 3rem;
        width: 100px;
        height: 100px;
    }
    div:nth-child(4) {
        margin-left: 3rem;
        width: 100px;
        height: 100px;
    }
    div:nth-child(5) {
        margin-left: 1.75rem;
        width: 80px;
        height: 80px;
    }
    div:nth-child(6) {
        margin-left: 0rem;
        width: 60px;
        height: 60px;
    }

    @media screen and (min-width: 2000px) {
        div:nth-child(1) {
            margin-left: 0rem;
            margin-bottom: 0.5rem;
            width: 100px;
            height: 100px;
        }
        div:nth-child(2) {
            margin-left: 6rem;
            margin-bottom: 0.5rem;
            width: 120px;
            height: 120px;
        }
        div:nth-child(3) {
            margin-left: 10rem;
            margin-bottom: 0.5rem;
            width: 180px;
            height: 180px;
        }
        div:nth-child(4) {
            margin-left: 10rem;
            margin-bottom: 0.5rem;
            width: 180px;
            height: 180px;
        }
        div:nth-child(5) {
            margin-left: 6rem;
            margin-bottom: 0.5rem;
            width: 120px;
            height: 120px;
        }
        div:nth-child(6) {
            margin-left: 0.5rem;
            margin-bottom: 0.5rem;
            width: 100px;
            height: 100px;
        }
    }
    
    @media screen and (min-width: 2400px) {
        div:nth-child(1) {
            margin-left: 0rem;
            margin-bottom: 0.5rem;
            width: 150px;
            height: 150px;
        }
        div:nth-child(2) {
            margin-left: 6rem;
            margin-bottom: 0.5rem;
            width: 170px;
            height: 170px;
        }
        div:nth-child(3) {
            margin-left: 10rem;
            margin-bottom: 0.5rem;
            width: 240px;
            height: 240px;
        }
        div:nth-child(4) {
            margin-left: 10rem;
            margin-bottom: 0.5rem;
            width: 240px;
            height: 240px;
        }
        div:nth-child(5) {
            margin-left: 6rem;
            margin-bottom: 0.5rem;
            width: 170px;
            height: 170px;
        }
        div:nth-child(6) {
            margin-left: 0.5rem;
            margin-bottom: 0.5rem;
            width: 150px;
            height: 150px;
        }
    }

    @media screen and (max-width: 2000px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;
        
        div {
            margin: 1rem;
        }
    }
}

.app_header-img {
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;

    img {
        width: 100%;
        object-fit: contain;
        z-index: 1;
        border-radius: 50%;
    }

    .overlay_circle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        // height: 100%;
    }

    @media screen and (max-width: 1200px) {
        margin: 2rem 0rem;
    }
}
