.app__works {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin: 4rem 0 2rem;

    .app__work-filter-item {
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        background-color: #fff;
        color: #000;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover {
            color: #fff;
            background-color: #000;
            scale: 1.2;
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;
            margin: 1rem;
        }
    }
    .item-active {
        color: #fff;
        background-color: #000;
        scale: 1;
    }
}

.app__work-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item {
        flex-direction: column;
        margin: 2rem;
        padding: 1rem;
        border-radius: 0.5rem;
        background-color: white;
        color: #000;
        width: 270px;
        height: 480px;
        transition: all 0.3s ease;

        &:hover {
            box-shadow: 0 0 25px rgba($color: #000000, $alpha: 0.2);
            scale: 1.2
        }

        @media screen and (min-width: 2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
            height: auto;
        }

        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }
}

.app__work-img {
    width: 100%;
    height: 230px;
    position: relative;
    img {
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        height: 350px;
    }
}

.app__work-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.4);
    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    div {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: #fff; 
        color: #fff;
        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;
        transition: all 0.3s ease;
    }

    svg {
        height: 50%;
        width: 50%;
        color: #000
    }
}

.app__work-content {
    padding: 0.5rem;
    position: relative;
    flex-direction: column;
    width: 100%;
    justify-content: center;

    h4 {
        margin-top: 1rem;
        line-height: 1.5;
    }

    .app__work-tag {
        position: absolute;
        border-radius: 0.5rem 1rem; 
        background-color: #fff;
        top: -25px
    }
}